.home_section {
  font-family: "Montserrat", sans-serif;
  display: flex;
  padding: 30px;
  padding-left: 100px;
  padding-right: 100px;
  padding-top: 70px;
}

.banner {
  font-family: "Montserrat", sans-serif;
  background-image: url("https://images.unsplash.com/photo-1552058461-dc1828d58bf6?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=747&q=80");
  height: 53vh;
  position: relative;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.banner-info {
  background-color: black;
  color: white;
  padding-top: 25vh;
  padding-left: 50px;
  padding-right: 50px;
  padding-bottom: 40px;
  width: 350px;
}

.banner-info h1 {
  font-size: 25px;
}
.banner-info h5 {
  font-size: 16px;
  font-weight: 400;
}

.banner-info button {
  background-color: #ff7779;
  color: white;
  text-transform: inherit;
  margin-top: 20px;
  font-weight: 600;
}

.banner-info button:hover {
  border: 1.5px solid #ff7779;
}

.banner-info h5 {
  margin-top: 10px;
}

.banner-search {
  display: flex;
  flex-direction: column;
}

.banner-search-btn {
  background-color: white !important;
  font-weight: 900 !important;
  text-transform: inherit !important;
  color: #ff7779 !important;
}
