.search {
  position: absolute;
  top: 35px;
  left: 25%;
  width: 50vw;
}

.search > h2 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 559px;
  padding: 10px;
  background-color: white;
  position: absolute;
  left: 0;
  top: 340px;
}

.search > input {
  width: 539px;
  padding: 20px;
  position: absolute;
  left: 0;
  height: 30px;
  top: 380px;
  border: none;
}

.search > input:focus {
  outline: 0;
}

.search > button {
  position: absolute;
  left: 0;
  top: 440px;
  text-transform: inherit !important;
  background-color: #ff7779;
  color: white;
  width: 579px;
}

.search > button:hover {
  background-color: white;
  color: #ff7779;
}

.card {
  margin: 10px;
  border: 0px solid black;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0px 6px 18px -9px rgba(0, 0, 0, 0.75);
  transition: -webkit-transform 100ms ease-in;
  transition: transform 100ms ease-in;
  transition: transform 100ms ease-in, -webkit-transform 100ms ease-in;
  cursor: pointer;
}

.card:hover {
  -webkit-transform: scale(1.07);
          transform: scale(1.07);
}

.card img {
  object-fit: fill;
  min-width: 200px;
  min-height: 200px;
  width: 100%;
}

.card-info {
  margin-top: -9px;
  border-radius: 10px;
  padding: 10px;
  padding-top: 20px;
  border: 1;
}

.card-info h2 {
  font-size: 18px;
  font-weight: 600;
}

.card-info h4 {
  font-size: 14px;
  font-weight: 300;
  margin-top: 8px;
  margin-bottom: 8px;
}

*,
*::after,
*::before {
  padding: 0;
  margin: 0;
}

.header {
  font-family: "Montserrat", sans-serif;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: white;
  z-index: 100;
  width: 100%;
}

.header-img {
  width: 100px;
  object-fit: contain;
  margin-left: 20px;
  height: 100px;
}

.header-center {
  display: flex;
  align-items: center;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  padding: 10px;
  height: 30px;
  border: 1px solid lightgrey;
  border-radius: 999px;
}

.header-center input {
  outline-width: 0;
  outline: none;
  border: none;
  width: 250px;
  padding: 10px;
  max-height: -webkit-fit-content;
  max-height: -moz-fit-content;
  max-height: fit-content;
}

.header-right {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 15vw;
  margin-right: 80px;
}
